/* FOOTER */
.footer {
  width: 100%;
  background: #00121b;
  display: block;
  bottom: 0;
}

.inner-footer {
  width: 95%;
  margin: auto;
  padding: 30px 10px;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: center;
}

.footer-items {
  width: 25%;
  padding: 10px 20px;
  box-sizing: border-box;
  color: #fff;
}

.footer-items p {
  font-size: 16px;
  text-align: justify;
  line-height: 25px;
  color: #fff;
}

.footer-items h1 {
  color: #fff;
}

.border1 {
  height: 3px;
  width: 40px;
  background: #e00b1b;
  color: #e00b1b;
  background-color: #e00b1b;
  border: 0px;
}

.footer-ul {
  list-style: none;
  color: #fff;
  /* font-size: 15px; */
  letter-spacing: 0.5px;
}

.footer-ul a {
  text-decoration: none;
  outline: none;
  color: #fff;
  transition: 0.3s;
}

.footer-ul a:hover {
  color: #e00b1b;
}

.footer-ul li {
  margin: 10px 0;
  height: 25px;
}

.footer-ul li i {
  margin-right: 20px;
}

.social-media {
  width: 100%;
  color: #fff;
  text-align: center;
  font-size: 20px;
}

.social-media a {
  text-decoration: none;
}

.social-media i {
  height: 25px;
  width: 25px;
  margin: 20px 10px;
  padding: 4px;
  color: #fff;
  transition: 0.5s;
}

.social-media i:hover {
  transform: scale(1.5);
}

.footer-bottom {
  padding: 10px;
  background: #00121b;
  color: #fff;
  font-size: 12px;
  text-align: center;
}

/* for tablet mode view */

@media screen and (max-width: 1275px) {
  .footer-items {
    width: 50%;
  }
}

/* for mobile screen view */

@media screen and (max-width: 660px) {
  .footer-items {
    width: 100%;
  }
  .footer {
    width: 100%;
    background: #00121b;
    /* display: block; */
    /* position: fixed;
    bottom: 0; */
  }
}

/* you can toggle the media screen view accordingly by changing the (max-width: px) to your convenience */

